import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import IndexScreen from './pages/IndexScreen';
import LoginScreen from './pages/LoginScreen';
import DetailsScreen from './pages/DetailsScreen';
import OrderScreen from './pages/OrderScreen';
import IndexOtherScreen from './pages/IndexOtherScreen';


const App: React.FC = () => (
    <Router>
        <Routes>
            <Route path="/" Component={IndexScreen} />
            <Route path="/IndexOtherScreen" Component={IndexOtherScreen} />
            <Route path="/DetailsScreen" Component={DetailsScreen} />
            <Route path="/OrderScreen" Component={OrderScreen} />
            <Route path="/LoginScreen" Component={LoginScreen} />
        </Routes>
    </Router>
);

export default App;
