import { createIntl, createIntlCache, RawIntlProvider, IntlProvider} from "react-intl";
import English from "./locales/en.json";
import Chinese from "./locales/zh.json";
import React, {useState} from 'react';

const messages = {
    en: English,
    zh: Chinese
};

const LanguageContext = React.createContext();

const I18nProvider = ({ children }) => {
    const [locale, setLocale] = useState(localStorage.getItem('language') ? localStorage.getItem('language') : 'zh');

    const changeLanguage = (languageCode) => {
        setLocale(languageCode)
    };

    return (
        <LanguageContext.Provider value={changeLanguage}>
            <IntlProvider locale={locale} messages={messages[locale]}>
                {children}
            </IntlProvider>
        </LanguageContext.Provider>
    );
};

export {I18nProvider, LanguageContext}
